.App {
  text-align: left;
}

.App-header {
  background-color: #262E49;
  height: 72px;
  color: #fff;
  font-size: 32px;
  line-height: 72px;
  padding-left: 32px;
}
.Menu ul{
  list-style: none;
  margin:2em 1em 0 1em;
  padding: 0;
}
.Menu ul>li{
    display:inline-block;
    text-align: center;
    line-height: 2em;
    padding: 0.2em 0;
    margin: 0 1em;
    color: #BFBFBF;
    border-bottom: none;
    font-size: 20px;
    font-weight: 800;

}
.Menu ul>li.activeMenu{
  color: #000;
  border-bottom: 4px solid #000;
  font-weight: 800;
}
.SearchBar ul{
  list-style: none;
  margin:1em 1em 1em 2em;
  padding: 0;
}
.SearchBar ul>li{
  display:inline-block;
  padding: 0.2em 0;
  margin: 0 0.5em;
}
.SearchBar select{
  min-width: 8em;
  min-height: 2em;
  padding: 0 0 0 0.5em;
  border-radius: 5px;
  font-size: 20px;
}
.SearchBar bottom{
  background-color: #262E49;
  color: #fff;
  padding: 0.5em 1em;
  border-radius: 5px;
}
.SearchBar img{
  line-height: 2em;
}

.IndexContent ul{
  list-style: none;
  margin:1em 1em 1em 2em;
  padding: 0;
}
.IndexContent ul>li{
  display:inline-block;
  padding: 0.2em 0;
  margin: 0 0.5em;
  vertical-align: top;
}
.IndexContent ul>li:nth-child(1),.IndexContent ul>li:nth-child(3){
  width: 20vw;
}
.IndexContent ul>li:nth-child(2){
  width: 50vw;
}
.IndexContent ul>li:nth-child(1) img{
  width: 100%;
}
.IndexContent ul>li:nth-child(2){
  text-align: center;
}
.IndexContent ul>li:nth-child(2) img{
  height: 587px;
}
.IndexContent .Tips1, .IndexContent .Tips2{
  background-color: #DEE0E4;
  border-radius: 5px;
  padding: 1em;
  margin-bottom: 1em;
}

.IndexContent .Tips1 .Line1, .IndexContent .Tips2 .Line1{
  line-height: 24px;
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 0.5em;
}

.Tips1Icon{
  width: 13vw;
  padding: 2em 1em 0 1em;
}
.Tips2Icon{
  width: 10vw;
  padding: 2em 1em 0 2em;
}